.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 60px;
}
.header .logo h1 {
  color: #ecdfd7;
}
.header .navigation {
  display: flex;
  justify-content: space-between;
  width: 40%;
  font-size: 18px;
}
.header .search-conainer {
  width: 190px;
}
.header .search-conainer .search {
  float: right;
  display: flex;
  border-radius: 20px;
  height: 25px;
  align-items: center;
  overflow: hidden;
  padding: 5px;
}
.header .search-conainer .search input {
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  width: 0;
  outline: none;
  font-size: 18px;
  text-indent: 10px;
  color: #ecdfd7;
  font-weight: bold;
  height: 22px;
  -webkit-transition: all 0.7s;
  transition: all 0.7s;
}
.header .search-conainer .search i {
  margin-right: 3px;
  color: #ecdfd7;
  font-size: 22px;
}
.header .search-conainer .search:hover {
  cursor: pointer;
  background-color: #af7761;
}
.header .search-conainer .search:hover input {
  visibility: visible;
  width: 150px;
}
