.App {
  height: 100%;
  background-color: #493939;
}
.App .footer {
  position: fixed;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.App .footer .content {
  background: url(data:image/png;base64,) no-repeat;
  padding-left: 16px;
  font-size: 12px;
}
.App a,
.App h1,
.App span {
  color: #ecdfd7;
}
.App a {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  padding-bottom: 1px;
  border-bottom: 1px solid rgba(0, 0, 0, 0);
}
.App a:hover {
  border-bottom: 1px solid #ecdfd7;
}
.App .hover:hover {
  cursor: pointer;
}
